import DateUtil from "@/mixins/date";
import mIcon from "@/components/m-icon/m-icon.vue"
import {CouponState,ApiCouponType} from "./couponType"

@Component({
    components:{
        mIcon
    }
})
export default class Coupon extends Vue implements CouponState {
    @Prop()
    coupon:ApiCouponType
    @Prop()
    state:string
    color= ["#ff7800", "#ff0108", "#54bb62", "#00c9e3"]
    leftColor= ["#ffeee5 ", "#ffebec", "#edfff0 ", "#e5fcff"]
    rightColor= ["#fefcfa", "#fff9f9", "#fbfdfc", "#f9fdfd"]
    lineColor= ["#fae0cd", "#fddde1", "#ccfadd", "#cdf0fa"]
    useType= false
    effectiveStartTime= ""
    effectiveEndTime= ""
    @Watch("coupon",{
        immediate:true,
        deep:true,
    })
    couponChange(){
        if (this.coupon) {
        const coupon = this.coupon;
        let effectiveStartTime = this.effectiveStartTime;
        let effectiveEndTime = this.effectiveEndTime;
        if (coupon.startTime) {
            effectiveStartTime = new DateUtil("").getYMDs(coupon.startTime);
            effectiveEndTime = new DateUtil("").getYMDs(coupon.endTime);
        } else {
            effectiveStartTime = new DateUtil("").getYMDs(coupon.createTime);
            effectiveEndTime = new DateUtil("").getAddDays(
            coupon.createTime,
            coupon.effectiveDays,
            );
        }
            this.effectiveStartTime= effectiveStartTime.replace(/-/g, ".")
            this.effectiveEndTime=effectiveEndTime.replace(/-/g, ".")
            this.useType= coupon.hasOwnProperty("canUse") ? !coupon.canUse : false
        }
    }
    /**
     * 去使用优惠券
     */
    gotoUse(e) {
        const item = e.currentTarget.dataset.item;
        this.$emit("gotoUse", { item }, {});
    }

    myCouponGet() {
        this.$emit("myCouponGet", this.$props.coupon, {});
    }

    /**
     * 选择优惠券使用
     */
    choose({
        currentTarget: {
        dataset: { item },
        },
    }) {
        this.$emit("catchchooseCoupon", item, {});
    }
}
